.App {
  font-family: sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.Teamsheet-logo {
  height: 7vmin;
  pointer-events: none;
  margin-top: 5px;
}


.Teamsheet-link {
  color: #ffffff;
}

section {
  height: 100vh;
  padding-top: 8vh;
  padding-bottom: 8vh;
  scroll-snap-align: start;
  background-color:#242424;
  color: white;
  text-align: center;
}

.Profile-big {
  height: 40vmin;
  border-radius: 40vmin;
}

.Button-test {
  cursor: pointer;
  margin-left: auto;
  margin-right: auto;
  width:150px;
  height:40px;
  text-align:center;
  border: 3px white solid;
  border-radius: 5px;
  font-weight: 600;
  background: linear-gradient(to bottom, #5ce1e6 50%, rgb(0, 0, 0) 50%);
  background-size: 100% 200%;
  background-position: bottom;
  transition: all .35s ease-out;
}

.Button-test:hover {
  background-position: top;
  color: rgb(0, 0, 0);
 }

 .Button-test .Arrow {
  transition: .3s;
 } 

 .Button-test:hover .Arrow {
  transform: rotateY(360deg);
 } 

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


