.Header-component {
    height: 8vh;
    background-color:#242424;
    text-align: right;
    position: sticky;
    top: 0;
    z-index: 100;
}

.Tool-button {
    color: white;
    background-color: transparent;
    border: none;
    font-weight: bold;
    font-size: 20px;
    margin-top: 2vh;
    margin-right: 30px;
    display: inline-block;
}

.Tool-button:hover {
    cursor:pointer;
   }

@media (max-width: 600px) {
    .Tool-button {
        font-size: 12px;
        margin-right: 5px;
    }
}

.active {
    border-color:#5ce1e6 !important;
    border-bottom: solid 3px #5ce1e6 !important;
    border-radius: 3px;
}